@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #313439;
  color: white; }

form {
  grid-area: form; }
  form .container-input {
    display: block;
    margin: 0 auto; }
    form .container-input label {
      margin-right: 1rem;
      color: #90b1aa; }
    form .container-input input {
      display: inline-block;
      width: 14rem;
      background: transparent;
      border: none;
      border-bottom: 1px solid #526b66;
      color: whitesmoke;
      margin-bottom: 1rem;
      padding-left: 0.5rem;
      padding-right: 0.5rem;
      font-size: 16px;
      font-family: 'Roboto Condensed', sans-serif; }
    form .container-input input:focus {
      outline: none; }
  form .container-input:nth-child(1) {
    margin-right: 0.5rem; }
  form button {
    display: block;
    background-color: #d84e5b;
    color: white;
    width: 8rem;
    height: 2rem;
    border: none;
    font-size: 14px;
    cursor: pointer;
    margin: 0 auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 5px; }

.company-title {
  border: 1px solid #526b66;
  border-radius: 5px;
  padding: 0.5rem;
  padding-left: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 1rem;
  font-size: 16px;
  position: relative; }
  .company-title:hover {
    color: #d84e5b; }
  .company-title .toggle-icon {
    position: absolute;
    right: 1rem;
    border-radius: 4px;
    padding: 0.5rem;
    line-height: 0.1rem;
    font-weight: lighter; }
  .company-title ul {
    cursor: default;
    margin: 1rem 0 1rem 1rem; }
    .company-title ul .company-details {
      margin-top: 0.5rem;
      font-size: 14px;
      color: #90b1aa;
      font-weight: normal; }

.results-container {
  max-width: 35rem;
  margin: 2rem auto;
  padding: 1rem;
  grid-area: results;
  text-align: left; }
  .results-container .query-message {
    text-align: center;
    color: #90b1aa; }
    .results-container .query-message .query {
      font-size: 20px;
      color: whitesmoke; }
  .results-container ul {
    list-style-type: none;
    padding-left: 0; }

.homepage-container {
  max-width: 35rem;
  margin: 2rem auto;
  display: grid;
  padding: 1rem;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "title title"  "desc desc"  "form form" "error error" "results results"; }
  .homepage-container .homepage-title {
    grid-area: title; }
  .homepage-container .homepage-description {
    grid-area: desc; }
  .homepage-container .error-message {
    grid-area: error;
    color: #d84e5b;
    margin: 0 auto; }

.App {
  text-align: center; }

